import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { AccommodationReviews } from '@schemas/modules/accommodations/accommodation-reviews.model';
import { AccommodationsDataService } from '@services/modules/accommodations/dataservices/accommodations.data.service';

@Component({
    selector: 'tz-acc-detail',
    templateUrl: './acc-detail.component.html',
    styleUrls: ['./acc-detail.component.scss'],
})
export class AccDetailComponent implements OnInit {
    @Input() accommodation!: Accommodation;

    @Output() backTolist: EventEmitter<void> = new EventEmitter<void>();
    @Output() showRooms: EventEmitter<void> = new EventEmitter<void>();

    public accommodationReviews!: AccommodationReviews;

    constructor(private accommodationsDataService: AccommodationsDataService) {}

    ngOnInit() {
        this.accommodationsDataService
            .getAccommodationReviews(this.accommodation)
            .subscribe((accommodationReviews: AccommodationReviews) => {
                this.accommodationReviews = { ...accommodationReviews };
            });
    }

    public backToResults(): void {
        this.backTolist.emit();
    }

    public showAccRooms(): void {
        this.showRooms.emit();
    }
}
