import {
    ChangeDetectorRef,
    Component,
    ElementRef,
    Inject,
    Injector,
    OnDestroy,
    OnInit,
    ViewChild,
} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { GeneralSettings } from '@core/constants/general-settings';
import { ServiceId } from '@core/constants/service-id.const';
import { ToolsService } from '@core/utils/tools-service';
import { MakerTracker, MapResult, MapResults } from '@schemas/modules/result-map/results-map.model';
import { ResultService } from '@services/modules/core/dataservices/result.service';
import { Map, ScaleControl, LngLatLike, Popup } from 'mapbox-gl';
import { Subject } from 'rxjs';
import { TzCurrencyPipe } from '@shared/pipes/tz-currency.pipe';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { PlatformService } from '@services/platform.service';
import { DialogService } from '@services/dialog.service';

@Component({
    selector: 'tz-map',
    templateUrl: './map.component.html',
    styleUrls: ['./map.component.scss'],
})
export class TzMapComponent implements OnInit, OnDestroy {
    @ViewChild('resultsContainer') resultsContainer?: ElementRef;

    public mapStyle = GeneralSettings.mapbox.mapStyle;
    public mapboxRef!: Map;
    public hideResults = false;
    public selectedAccommodation: Accommodation | undefined;
    public markersList: MakerTracker[] = [];
    public mapConfig: {
        serviceId: ServiceId;
        mapCenterPosition: {
            lng: number;
            lat: number;
        };
        selectedHotelId: string | undefined;
    };
    public mapResults!: MapResults;
    public selectedId = '';
    public searchedAddress!: LngLatLike;
    public platformService!: PlatformService;

    protected mapCorners!: {
        topLeft: {
            lat: number;
            lng: number;
        };
        bottomRight: {
            lat: number;
            lng: number;
        };
    };
    private destroy$: Subject<boolean> = new Subject<boolean>();
    private popup = new Popup({
        closeButton: false,
    });
    private readonly changeDetectorRef!: ChangeDetectorRef;

    constructor(
        injector: Injector,
        @Inject(MAT_DIALOG_DATA) public data: any,
        protected mdDialogRef: MatDialogRef<TzMapComponent>,
        protected resultService: ResultService,
        protected tzCurrencyPipe: TzCurrencyPipe,
        protected searchDataService: SearchDataService,
        protected dialogService: DialogService
    ) {
        this.mapConfig = ToolsService.deepCopy(data);
        this.changeDetectorRef = injector.get(ChangeDetectorRef);
        this.platformService = injector.get(PlatformService);
    }

    ngOnInit() {
        this.initiateMapComponent();
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public onMapLoad(map: Map): void {
        this.mapboxRef = map;
        this.mapboxRef.addControl(
            new ScaleControl({
                maxWidth: 80,
                unit: 'metric',
            })
        );

        this.addMapEvents();
    }

    public markerTrackId(index: number, marker: MapResult): string {
        return marker.featureKey;
    }

    public toggleResultsList(): void {
        if (!this.resultsContainer) {
            return;
        }

        this.hideResults = !this.hideResults;
        this.resultsContainer.nativeElement.style.display = this.hideResults ? 'none' : 'block';
        this.mapboxRef.resize();
    }

    public zoomMap(zoomIn = false): void {
        this.mapboxRef.setZoom(this.mapboxRef.getZoom() + (zoomIn ? 0.5 : -0.5));
    }

    public closeMap(selected: boolean): void {
        const selectedHotel = selected && this.selectedAccommodation ? this.selectedAccommodation : undefined;
        this.dialogService.closeDialog(this.mdDialogRef, selectedHotel);
    }

    public onMarkerSelected(marker: MapResult): void {
        this.selectedId = marker.featureKey;
        if (this.platformService.isMobileDevice) {
            this.changeDetectorRef.detectChanges();
        }

        this.fetchSelectedMarkerData();
    }

    public resetSelection(): void {
        this.selectedId = '';
        this.selectedAccommodation = undefined;

        if (this.platformService.isMobileDevice) {
            this.changeDetectorRef.detectChanges();
        }
    }

    public onMouseOver(marker: MapResult): void {
        if (this.platformService.isMobileDevice || this.selectedId === marker.featureKey) {
            return;
        }

        const latLng = marker.coordinates as LngLatLike;
        this.popup.setLngLat(latLng).setHTML(this.buildPopupHtmlStr(marker)).addTo(this.mapboxRef);
    }

    public onMouseLeave(): void {
        this.popup.remove();
    }

    protected addMapEvents(): void {
        this.mapboxRef.on('zoomend', () => {
            this.requestNewMarkers();
        });

        this.mapboxRef.on('dragend', () => {
            this.requestNewMarkers();
        });

        this.mapboxRef.on('click', () => {
            this.resetSelection();
        });
    }

    protected buildPopupHtmlStr(marker: MapResult): string {
        const imageSize = marker.properties.imageProvider === 'Ostrovok' ? '240x240' : 'medium';
        let starsStr = '';
        for (let i = 0; i < marker.properties.star; i++) {
            starsStr += '<img src="/assets/icons/web/icons/star.svg" alt="">';
        }

        const params = {
            image: marker.properties.imageUrl.replace('{size}', imageSize),
            name:
                marker.properties.name.length > 30
                    ? marker.properties.name.substring(0, 30) + '... '
                    : marker.properties.name,
            starsStr,
            address: marker.properties.address,
            price: this.tzCurrencyPipe.transform(
                marker.properties.cheapestPrice.listPrice,
                marker.properties.cheapestPrice.listPriceCur
            ),
        };

        return `<div class="tz-map-result-block">
                    <img class="hotel-image" src="${params.image}" alt="" />
                    <div class="hotel-info">
                        <div class="hotel-name">${params.name}
                            <div class="hotel-stars">${params.starsStr}</div>
                        </div>
                        <div class="hotel-address">${params.address}</div>
                        <div class="hotel-price">${params.price}</div>
                    </div>
                </div>`;
    }

    protected createMarker(marker?: MapResult, markerClass?: string): HTMLElement {
        const markerEl = document.createElement('div') as HTMLElement;
        markerEl.classList.add('tz-map-marker');
        if (markerClass) {
            markerEl.classList.add(markerClass);
        }

        return markerEl;
    }

    protected fetchMapData(): void {
        const queryParams = { selected_id: this.selectedId };
        const request = { ...this.mapCorners, center: this.mapConfig.mapCenterPosition };

        this.resultService.searchMapResults(this.mapConfig.serviceId, queryParams, request).subscribe((results) => {
            this.mapResults = results;
        });
    }

    protected initiateMapComponent(): void {
        this.mapCorners = {
            topLeft: {
                lat: this.mapConfig.mapCenterPosition.lat + 0.055968722,
                lng: this.mapConfig.mapCenterPosition.lng - 0.111450424,
            },
            bottomRight: {
                lat: this.mapConfig.mapCenterPosition.lat - 0.066414125,
                lng: this.mapConfig.mapCenterPosition.lng + 0.105686722,
            },
        };

        if (this.mapConfig.selectedHotelId) {
            this.selectedId = this.mapConfig.selectedHotelId;
            this.fetchSelectedMarkerData();
        }

        const accSearchData = this.searchDataService.getSearchData(this.mapConfig.serviceId);
        if (this.mapConfig.serviceId === ServiceId.ACC && accSearchData && accSearchData.address) {
            this.searchedAddress = {
                lng: accSearchData.address.longitude,
                lat: accSearchData.address.latitude,
            };
        }

        this.fetchMapData();
    }

    private fetchSelectedMarkerData(): void {
        const queryParams = { selected_id: this.selectedId };
        this.resultService.findResultById(this.mapConfig.serviceId, queryParams).subscribe((selectedResult) => {
            this.selectedAccommodation = selectedResult;
        });
    }

    private requestNewMarkers(): void {
        const MAP_PREFETCH_SIZE = 100; // increase/decrease size in pixels of map bounding sent on server data requests
        const canvas = this.mapboxRef.getCanvas();
        const cUL = this.mapboxRef.unproject([-MAP_PREFETCH_SIZE, -MAP_PREFETCH_SIZE]);
        const cLR = this.mapboxRef.unproject([canvas.width + MAP_PREFETCH_SIZE, canvas.height + MAP_PREFETCH_SIZE]);

        this.mapCorners = {
            topLeft: cUL,
            bottomRight: cLR,
        };
        this.fetchMapData();
    }
}
