import {
    FilterItem,
    FilterItemCheckbox,
    FilterReferenceItem,
    FilterSlider,
} from '@schemas/modules/core/filters/service-filter';

export class ResultsFiltersUtils {
    constructor() {}

    public static selectedFiltersCounter(filters: FilterReferenceItem): number {
        if (filters[0].ref_id === 'TRAIN_STOPS' || filters[0].ref_id === 'FLIGHT_STOPS') {
            return this.selectedFiltersCounterFlightTrain(filters);
        }

        return this.selectedFiltersCounterDefault(filters);
    }

    private static selectedFiltersCounterFlightTrain(filters: FilterReferenceItem): number {
        let selectedFiltersCount = 0;
        const stationsFilters: Partial<FilterItemCheckbox | FilterSlider>[] = [];

        filters.forEach((filterItem: FilterItem) => {
            if (filterItem.ref_id === 'FLIGHT_TIME_AIRPORTS' || filterItem.ref_id === 'TRAIN_TIME_STATIONS') {
                filterItem.sections?.forEach((section) =>
                    section.ogs.map((filter: FilterItem) => {
                        if (filter.checkboxes) {
                            stationsFilters.push(
                                ...filter.checkboxes.filter((checkbox: FilterItemCheckbox) => !checkbox.disabled)
                            );
                        } else if (filter.slider) {
                            stationsFilters.push(filter.slider);
                        }
                    })
                );
                selectedFiltersCount += stationsFilters.reduce((acc, filter) => acc + Number(filter.selected), 0);
            } else {
                if (filterItem.checkboxes) {
                    selectedFiltersCount += filterItem.checkboxes
                        .filter((checkbox: FilterItemCheckbox) => !checkbox.disabled)
                        .reduce((acc: number, checkbox: FilterItemCheckbox) => acc + Number(checkbox.selected), 0);
                } else if (filterItem.slider) {
                    selectedFiltersCount += Number(filterItem.slider.selected);
                }
            }
        });

        return selectedFiltersCount;
    }

    private static selectedFiltersCounterDefault(filters: FilterReferenceItem): number {
        let selectedFiltersCount = 0;
        filters.forEach((filterItem: FilterItem) => {
            if (filterItem.checkboxes) {
                selectedFiltersCount += filterItem.checkboxes.reduce(
                    (acc: number, checkbox: FilterItemCheckbox) => acc + Number(checkbox.selected),
                    0
                );
            } else if (filterItem.slider) {
                selectedFiltersCount += Number(filterItem.slider.selected);
            }
        });

        return selectedFiltersCount;
    }
}
