import { Component, Input, OnInit } from '@angular/core';
import { MapResult, MarkerProperty } from '@schemas/modules/result-map/results-map.model';

@Component({
    selector: 'tz-acc-list-item',
    templateUrl: './acc-list-item.component.html',
    styleUrls: ['./acc-list-item.component.scss'],
})
export class AccListItemComponent implements OnInit {
    @Input() accommodationMapInfo!: MapResult;

    public hotel!: MarkerProperty;
    constructor() {}

    ngOnInit() {
        this.hotel = this.accommodationMapInfo.properties;
    }

    public starsArray(i: number) {
        return new Array(i);
    }
}
