import { Component, Input, Output, OnInit, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
import { MapResult, MarkerProperty } from '@schemas/modules/result-map/results-map.model';

@Component({
    selector: 'tz-mob-acc-list-item',
    templateUrl: './mob-acc-list-item.component.html',
    styleUrls: ['./mob-acc-list-item.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobAccListItemComponent implements OnInit {
    @Input() accommodationMapInfo!: MapResult;
    @Input() selectedItem = false;

    @Output() showRooms = new EventEmitter<void>();
    @Output() backTolist = new EventEmitter<void>();

    public accommodation!: MarkerProperty;
    constructor() {}

    ngOnInit() {
        this.accommodation = this.accommodationMapInfo.properties;
    }

    public starsArray(i: number) {
        return new Array(i);
    }

    public onSwipeDown(): void {
        if (!this.selectedItem) {
            return;
        }

        this.backTolist.emit();
    }
}
